"use client";
import { useState, useEffect } from "react";

export function useThemeColor(iframeRef: React.RefObject<HTMLIFrameElement | null>) {
  const [color, setColor] = useState<string>("#999995"); // Ÿæ€åÿ¥‚ÄåŸÅÿ±ÿ∂

  useEffect(() => {
    const iframe = iframeRef.current;
    if (!iframe) return;

    // ŸÑ€åÿ≥ÿ™ ÿ±ŸÜ⁄Ø‚ÄåŸáÿß€å ÿ≤€åÿ®ÿß ÿ®ÿ±ÿß€å ÿ™ÿ≥ÿ™
    const beautifulColors = [
      '#FF6B6B', // ŸÇÿ±ŸÖÿ≤ ÿ±Ÿàÿ¥ŸÜ
      '#4ECDC4', // ŸÅ€åÿ±Ÿàÿ≤Ÿá‚Äåÿß€å
      '#45B7D1', // ÿ¢ÿ®€å ÿ±Ÿàÿ¥ŸÜ
      '#96CEB4', // ÿ≥ÿ®ÿ≤ ÿ±Ÿàÿ¥ŸÜ
      '#FFEAA7', // ÿ≤ÿ±ÿØ ÿ±Ÿàÿ¥ŸÜ
      '#DDA0DD', // ÿ®ŸÜŸÅÿ¥ ÿ±Ÿàÿ¥ŸÜ
      '#98D8C8', // ÿ≥ÿ®ÿ≤ ÿ¢ÿ®€å
      '#FFB6C1', // ÿµŸàÿ±ÿ™€å ÿ±Ÿàÿ¥ŸÜ
      '#87CEEB', // ÿ¢ÿ®€å ÿ¢ÿ≥ŸÖÿßŸÜ€å
      '#F0E68C', // ÿ≤ÿ±ÿØ ⁄©ÿßŸá€å
      '#D8BFD8', // ÿ®ŸÜŸÅÿ¥ ⁄©ŸÖÿ±ŸÜ⁄Ø
      '#F5DEB3', // ÿ®⁄ò
    ];

    let colorIndex = 0;

    const changeColor = () => {
      const newColor = beautifulColors[colorIndex % beautifulColors.length];
      console.log(`üé® ÿ™ÿ∫€å€åÿ± ÿ±ŸÜ⁄Ø ÿ®Ÿá: ${newColor}`);
      setColor(newColor);
      colorIndex++;
    };

    const handleLoad = () => {
      console.log("üîÑ iframe ŸÑŸàÿØ ÿ¥ÿØ!");
      
      // Ÿáÿ± 3 ÿ´ÿßŸÜ€åŸá €å⁄© ÿ±ŸÜ⁄Ø ÿ¨ÿØ€åÿØ
      const interval = setInterval(changeColor, 3000);
      
      // ÿ®ÿπÿØ ÿßÿ≤ 30 ÿ´ÿßŸÜ€åŸá ŸÖÿ™ŸàŸÇŸÅ ⁄©ŸÜ
      setTimeout(() => {
        clearInterval(interval);
        console.log("‚èπÔ∏è ÿ™ÿ∫€å€åÿ± ÿ±ŸÜ⁄Ø ŸÖÿ™ŸàŸÇŸÅ ÿ¥ÿØ");
      }, 30000);
    };

    // ÿß⁄Øÿ± iframe ŸÇÿ®ŸÑÿßŸã ŸÑŸàÿØ ÿ¥ÿØŸá
    if (iframe.src) {
      handleLoad();
    }

    iframe.addEventListener("load", handleLoad);
    return () => iframe.removeEventListener("load", handleLoad);
  }, [iframeRef]);

  return color;
}
